*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body,
div,
table,
tr,
td,
h1,
h2,
h3,
li {
  position: relative;
}

body {
  font-family: "Gilroy", Arial, sans-serif;
  font-size: 16px;
  letter-spacing: 0.05em;
  line-height: 26px;
  font-weight: 400;
  color: #1f3f68;
  min-width: 970px;
  margin: 0;
}

blockquote,
q {
  quotes: none;
}

.clear {
  clear: both;
}

hr {
  width: 100%;
  height: 1px;
  margin: 40px 0;
  border: none;
  background-color: #ededee;
}

a {
  color: #5a98d0;
  text-decoration: none;
  border-bottom: 1px solid #cce3f1;
  outline: none;
  transition: all 0.3s;
}

a:hover {
  color: #009ee2;
  text-decoration: none;
  border-color: transparent;
}

a:focus {
  text-decoration: none;
  outline: none;
  border-color: transparent;
}

a:active {
  outline: none;
}

h1 {
  font-size: 36px;
  margin: 0;
  line-height: 36px;
  letter-spacing: 0.027em;
}

h2 {
  font-size: 36px;
  margin: 0 0 32px;
  line-height: 46px;
  letter-spacing: 0.027em;
}

h3 {
  font-size: 16px;
  margin: 20px 0 15px;
  line-height: 26px;
  letter-spacing: 0.027em;
}

p {
  margin: 0 0 15px;
}

ul {
  margin: 0 0 15px;
}

ul li {
  list-style: none;
}

ul li::before {
  content: "\2022";
  position: absolute;
  top: 0;
  left: -10px;
}

ol {
  margin: 0 0 15px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

table {
  width: 100%;
  margin: 0 0 15px;
  border: none;
  border-collapse: collapse;
  border-spacing: 0;
}

table tbody {
  border: none;
}

table tr.odd,
table tr.even {
  background-color: transparent;
}

table td,
table th {
  border: none;
  padding: 0 14px 0;
  line-height: 36px;
}

table.nostyle {
  border: none;
}

table.nostyle thead {
  background-color: transparent;
}

table.nostyle tr {
  background-color: transparent;
}

table.nostyle tr th,
table.nostyle tr td {
  border: none;
  padding: 0;
  line-height: inherit !important;
}

.container {
  width: auto;
  max-width: 1199px;
  margin: 0 auto;
}

.container-w940 {
  width: auto;
  max-width: 940px;
  margin: 0 auto;
}

.table-wrapper {
  overflow: auto;
}

a.lightgallery {
  border-bottom: none;
}

.margin {
  margin: 0 0 70px;
}

.margin-small {
  margin: 0 0 50px;
}

.padding {
  padding: 70px 0;
}

button {
  outline: 0;
}

button:focus {
  outline: none;
}

.btn {
  display: inline-block;
  position: relative;
  cursor: pointer;
  text-align: center;
  font-weight: 600;
  padding: 6px 14px 7px;
  letter-spacing: 0;
}

.btn_default {
  border-width: 1px;
  border-style: solid;
  transition: all 0.25s ease-in-out;
  text-decoration: none;
}

.btn_default:hover {
  text-decoration: none;
}

.btn_default:focus {
  text-decoration: none;
}

.btn_primary {
  border-width: 1px;
  border-style: solid;
  transition: all 0.25s ease-in-out;
  text-decoration: none;
}

.btn_primary:hover {
  text-decoration: none;
}

.btn_primary:focus {
  text-decoration: none;
}

.btn_link {
  background: none;
  border: none;
  border-bottom: 1px solid;
  font-weight: 400;
  padding: 0;
}

.btn_download {
  border: none;
  font-weight: 600;
  position: relative;
  padding-left: 42px;
  padding-top: 8px;
}

.btn_download::before {
  content: "\f56d";
  font-family: 'Font Awesome 5 Pro';
  font-size: 37px;
  font-weight: 100;
  line-height: 1;
  position: absolute;
  position: absolute;
  top: -2px;
  left: 0;
}

.btn_download span {
  border-bottom: 1px solid;
}

.btn_upper {
  text-transform: uppercase;
}

.btn_block {
  display: block;
  width: 100%;
}

.btn_lg {
  padding: .5rem 1rem;
  font-size: 1.25rem;
}

.btn_sm {
  padding: .25rem .5rem;
  font-size: .875rem;
}

.btn_radius {
  border-radius: 4px;
}

.btn_icon-left {
  padding-left: 54px;
}

.btn_icon-left::before {
  display: flex;
  align-items: center;
  position: absolute;
  top: 1px;
  bottom: 0;
  left: 18px;
}

.btn_icon-right {
  padding-right: 54px;
}

.btn_icon-right::before {
  display: flex;
  align-items: center;
  position: absolute;
  top: 1px;
  right: 18px;
  bottom: 0;
}

.btn_style-1 {
  width: 220px;
  background: #5a98d0;
  box-shadow: 5px 10px 20px rgba(53, 110, 173, 0.2);
  border-radius: 10px;
  font-family: "Gilroy";
  font-weight: 300;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  padding: 14px 20px 15px;
  color: white;
}

a.btn_style-1 {
  text-decoration: none;
  border-color: transparent;
  color: white;
}

a.btn_style-1:hover {
  color: rgba(255, 255, 255, 0.6);
}

input[type="text"],
input[type="password"],
input[type="email"],
textarea {
  width: 100%;
  padding: 9px 15px 11px;
  transition: all 0.3s ease-in-out;
  outline: none;
  font-size: 14px;
  box-shadow: none;
  border-radius: 7px;
  background-color: transparent;
}

input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
textarea:focus {
  outline: none;
}

input[type="text"]:focus::-webkit-input-placeholder,
input[type="password"]:focus::-webkit-input-placeholder,
input[type="email"]:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
  opacity: 0;
}

input[type="text"]:focus::-moz-placeholder,
input[type="password"]:focus::-moz-placeholder,
input[type="email"]:focus::-moz-placeholder,
textarea:focus::-moz-placeholder {
  opacity: 0;
}

input[type="text"]:focus:-moz-placeholder,
input[type="password"]:focus:-moz-placeholder,
input[type="email"]:focus:-moz-placeholder,
textarea:focus:-moz-placeholder {
  opacity: 0;
}

input[type="text"]:focus:-ms-input-placeholder,
input[type="password"]:focus:-ms-input-placeholder,
input[type="email"]:focus:-ms-input-placeholder,
textarea:focus:-ms-input-placeholder {
  opacity: 0;
}

textarea {
  resize: none;
}

input[type="submit"] {
  outline: none;
}

@media only screen and (max-width: 1199px) {
  .container {
    max-width: 970px;
  }

  .container-w940 {
    max-width: 970px;
  }
}

@media only screen and (max-width: 991px) {
  .table-m-column {
    display: block;
    margin-top: 20px;
    border: none;
  }

  .table-m-column tbody {
    display: block;
  }

  .table-m-column tr {
    display: block;
  }

  .table-m-column td,
  .table-m-column th {
    display: block;
    width: 100% !important;
    padding: 0;
  }

  .table-m-nobg tr {
    background-color: transparent !important;
  }

  .container {
    max-width: 750px;
  }

  .container-w940 {
    max-width: 750px;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    max-width: 570px;
  }

  .container-w940 {
    max-width: 570px;
  }
}

@media only screen and (max-width: 576px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }

  .container-w940 {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}